.spot-light {
  position: absolute;
  height: 95%;
  width: 80%;
  top: 5%;
  -webkit-filter: blur(50px);
          filter: blur(50px);
  border-radius: 50%;
  z-index: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(82,82,61,0.8)),to(rgba(82,82,61,0)));
  background: -webkit-linear-gradient(top, rgba(82,82,61,0.8) 0%,rgba(82,82,61,0) 100%);
  background: -o-linear-gradient(top, rgba(82,82,61,0.8) 0%,rgba(82,82,61,0) 100%);
  background: linear-gradient(to bottom, rgba(82,82,61,0.8) 0%,rgba(82,82,61,0) 100%);

}
@media screen and (orientation:landscape) {
  .spot-light {
    width: 60%;
  
  }
}


