.ticks {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.tick {
  width: 0.25vmin;
  height: 100%;
  position: absolute;
}

.tick div {
  height: 2vmin;
  width: 100%;
  position: relative;
  background-color: #906500;
  -webkit-transform: translate(0, 1vmin);
      -ms-transform: translate(0, 1vmin);
          transform: translate(0, 1vmin);
}

.tick:nth-child(5n+1) {
  width: 0.4vmin;
}

.tick:nth-child(5n+1) div {
  background-color: black;
  height: 6%;
}


